.form {
  background-color: #ffffff;
}
.form__fieldset {
  border: 0;
  padding: 0;
  margin: 0;
  padding-bottom: 17px;
}
.form__heading {
  color: #63d1bb;
  background-color: #ffffff;
  font-size: 17px;
  line-height: 16px;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
  display: block;
  margin-bottom: 24px;
}
.form__heading::after {
  content: "";
  position: absolute;
  top: 7px;
  right: 0;
  bottom: initial;
  left: initial;
  width: calc(100% - 60px);
  background-color: #63d1bb;
  height: 1px;
}
.form__options p {
  margin: 0;
  margin-bottom: 17px;
}
.form__options-contacts-wrapper {
  position: relative;
}
.form__options-textarea-wrapper {
  display: flex;
}
.form__label--radio,
.form__label--checkbox {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 40px;
  margin-bottom: 17px;
  padding-left: 62px;
  font-size: 17px;
  line-height: 18px;
  font-weight: 700;
  cursor: pointer;
}
.form__label--radio::before,
.form__label--checkbox::before {
  content: "";
  position: absolute;
  top: 0;
  right: initial;
  bottom: initial;
  left: 0;
  width: 40px;
  height: 40px;
  border: 2px solid #000000;
  box-sizing: border-box;
}
.form__label--radio:hover::before {
  border-color: #63d1bb;
}
.form__label--radio:active::before {
  border-color: #444444;
  opacity: 0.3;
}
.form__label--checkbox:hover::before {
  border-color: #63d1bb;
}
.form__label--checkbox:active::before {
  border-color: #444444;
  opacity: 0.3;
}
.form__label-wrapper {
  display: block;
}
.form__label--radio::before {
  border-radius: 50%;
}
.form__radio:checked + .form__label--radio::before {
  box-shadow: inset 0 0 0 10px #ffffff;
  background-color: #56c3b2;
}
.form__checkbox:checked + .form__label--checkbox::after {
  opacity: 1;
}
.form__label--checkbox::after {
  content: url("../img/icon-tick.svg");
  position: absolute;
  top: 8px;
  left: 4px;
  transform: scale(0.8);
  opacity: 0;
}
.form__label--text {
  font-size: 17px;
  line-height: 18px;
  font-weight: bold;
  padding-top: 4px;
  display: block;
}
.form__input--text {
  font-size: 17px;
  line-height: 18px;
  width: 100%;
  height: 50px;
  padding: 0;
  margin: 0;
  border: 0;
  border-bottom: solid 2px #000000;
  color: #444444;
}
.form__input--text:hover {
  border-color: #63d1bb;
}
.form__input--text::placeholder {
  color: #444444;
  opacity: 0.3;
}
.form__input--text:focus {
  border-color: #63d1bb;
}
.form__input--contacts {
  margin-bottom: 17px;
}
.form__input--contacts-icon {
  position: absolute;
  top: 11px;
  right: 8px;
  fill: #000000;
  opacity: 0.2;
}
.form__input--text:hover + .form__input--contacts-icon,
.form__input--text:focus + .form__input--contacts-icon {
  fill: #63d1bb;
  opacity: 1;
}
.form__textarea {
  width: 100%;
  height: 105px;
  border: solid 2px #000000;
  padding: 16px;
  color: #444444;
}
.form__textarea:hover {
