  margin: 0;
}
.form__send {
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  color: #444444;
  width: 100%;
  border: solid 2px #444444;
  background-color: #ffffff;
  padding: 14px;
  margin-top: 31px;
  margin-bottom: 55px;
