.page-catalog__wrapper {
  width: 100%;
  margin: 0 auto;
  background-color: @primary;
}

.page-catalog__title {
  font-size: 27px;
  line-height: 30px;
  text-align: center;
  background-color: @primary-light;
  margin: 0;
  padding: 70px 0 76px;
}

@media (min-width: @tablet-width) {
  .page-catalog__title {
    font-size: 43px;
    line-height: 50px;
    padding: 95px 0 92px;
    background-image: url(".././img/bg-zigzag-fill.svg");
    background-repeat: repeat-x;
    background-position: bottom;
    background-size: auto 24px;
  }
}

@media (min-width: @desktop-width) {
  .page-catalog__title {
    background-size: auto 34px;
    font-size: 53px;
    line-height: 57px;
    padding: 93px 0 122px;
    width: 100%;
  }
}
